.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {

  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {

  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.item {
  height: 100vh;
}

.cover {
  height: 50vh;
  width: 100%;
  position: relative;
  background-size: cover;
  z-index: 1;
}

.content {
  padding: 20px;
  overflow: hidden;
  height: 50vh;
}

.title {
  font-size: 26px;
  line-height: 34px;
  margin-bottom: 10px;
  font-family: serif;
  font-weight: 800;
}

.excerpt {
  font-size: 18px;
  line-height: 26px;
  font-family: serif;
}
